.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  background-color: #007bff;
  color: white;
  padding: 20px;
  margin-bottom: 20px;
}



.search-form {
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 5px;
  padding: 5px;
}

.search-input {
  padding: 5px;
  margin-right: 10px;
  border: none;
  outline: none;
}

.search-button {
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.sort-by,
.sort-order {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

label {
  margin-right: 10px;
  font-weight: bold;
}

select {
  padding: 5px;
  border-radius: 5px;
  border: none;
  outline: none;
}

  



/* pagination */

.pagination {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #fff;
    padding: 10px 0;
    box-shadow: 0 -1px 2px rgba(0, 0, 0, 0.1), 0 1px 2px rgba(0, 0, 0, 0.1);  }
  
  .pagination ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .pagination .item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
    margin: 0 5px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
  }
  
  .pagination .item:hover {
    background-color: #f9f9f9;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.3);
  }
  
  .pagination .item.active {
    background-color: #007aff;
    color: #fff;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.3);
  }
  
  .pagination .item.active:hover {
    background-color: #007aff;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.3);
  }
  
  .pagination .item:hover:not(.active) {
    background-color: #f9f9f9;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.3);
  }
  
